import React, { Suspense, useEffect, useRef, useState } from "react";
import { Canvas, useFrame, useLoader, useThree, extend } from '@react-three/fiber'
import * as THREE from "three";
import {Cylinder, Line, OrbitControls, Plane, Shadow, useTexture } from "@react-three/drei";

import './App.css';
import "bootstrap/dist/css/bootstrap.min.css";

import person1 from './person1.png';
import person2 from './person2.png';
import person3 from './person3.png';
import person4 from './person4.png';
import person5 from './person5.png';
import person6 from './person6.png';
import logo from './logo.png';

extend({ OrbitControls });

const CameraControls = () => {
  const { camera, gl: { domElement } } = useThree();
  const controls = useRef();
  useFrame((state) => controls.current.update());
  return <OrbitControls
    ref={controls}
    args={[camera, domElement]}
    enableZoom={false}
    maxPolarAngle={Math.PI/2}
    minPolarAngle={Math.PI/2}
  />;
};

const imageFetcher = (person) => {
  switch (person) {
    case 0:
      return person1;
    case 1:
      return person2;
    case 2:
      return person3;
    case 3:
      return person4;
    case 4:
      return person5;
    case 5:
      return person6;
    default:
      break;
  }
}

const persons = ["Andrew Scott", "Albert Ohama", "Brandy Taylor", "Mike Bighetti", "Richard Stevenson", "Chiang Ye"]
const positions = ["HR Manager", "Chief of RevOps", "Tehnical Advisor", "Founder", "Core Developer", "Chief of Operations"]

const Popup = ({person}) => {
  return (<div className="three-popup position-absolute m-3 p-3">
      <div className="d-flex justify-content-start align-items-center">
        <img src={imageFetcher(person)} alt="person" width="70px"/>
        <div className="d-flex flex-column justify-content-center align-items-start ml-3" >
          <div className="popupwrap">
            <div className="person">{persons[person]}</div>
            <div className="position">{positions[person]}</div>
          </div>
          <div className="mt-1">
            Relation:<span className={"ml-1 " + ((person !== 1 && person !== 5) ? "good" : "bad")}>{(person !== 1 && person !== 5) ? "Good" : "Bad"}</span>
          </div>
        </div>
      </div>
    </div>)
}

