function App() {
  const [a, setA] = useState(false);
  const [b, setB] = useState(false);
  const [c, setC] = useState(false);
  const [d, setD] = useState(false);
  const [e, setE] = useState(false);
  const [f, setF] = useState(false);

  return (
    <>
      {(a || b || c || d || e || f) && <Popup person={a ? 0 : (b ? 1: (c ? 2: (d ? 3 : (e ? 4 : 5))))}/>}
      <Canvas shadows colorManagement camera={{position: [0,0,4], fov: 120}}>
        <CameraControls />
        <fog attach="fog" args={['white', 0, 40]} />
        <ambientLight intensity={0.4} />
        <directionalLight
          castShadow
          position={[2.5, 8, 5]}
          intensity={1.5}
          shadow-mapSize-width={1024}
          shadow-mapSize-height={1024}
          shadow-camera-far={50}
          shadow-camera-left={-10}
          shadow-camera-right={10}
          shadow-camera-top={10}
          shadow-camera-bottom={-10}
        />
        <pointLight position={[-10, 0, -20]} color="#00aaff" intensity={2.5} />
        <pointLight position={[2, 0, -2]} color="red" intensity={2.5} />
        <pointLight position={[2, 0, 0]} color="00aaff" intensity={2.5} />
        <pointLight position={[0, 0, -5]} color="00ff00" intensity={5} />
        <pointLight position={[2, 2, 2]} color="#00aaff" intensity={5.5} />
        <pointLight position={[-10, 0, 20]} color="#00aaff" intensity={5} />
        <Suspense fallback={null}>
          <ShadowScene className="position-relative" aa={{a, setA}} bb={{b, setB}} cc={{c, setC}} dd={{d, setD}} ee={{e, setE}} ff={{f, setF}} />
        </Suspense>
      </Canvas>
    </>
  );
}

export default App;
